import { p as prisma } from "../../../../chunks/index4.js";
const load = async ({ locals }) => {
  const user = locals.user;
  const userData = await prisma.user.findUnique({
    where: {
      id: user?.id
    },
    include: {
      area: true
    }
  });
  return {
    user: userData,
    area: userData.area
  };
};
export {
  load
};
